// Import necessary modules from React and external libraries
import { useState, useEffect } from "react";
// Assuming these are from your shadcn/ui components
import { Button } from "./components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "./components/ui/card";
// Icons from lucide-react
import { Settings, X } from "lucide-react";
// ThemeProvider for managing themes, assumed to be part of next-themes or similar
import { ThemeProvider, useTheme } from "next-themes"; // Import useTheme for demonstration purposes

// Main WallCrunch component
export default function App() {
  return (
    // Wrap the WallCrunch component with ThemeProvider for theme management
    // defaultTheme="system" makes it respect the OS theme by default
    // enableSystem allows switching between light/dark based on system preference
    <ThemeProvider attribute="class" defaultTheme="system" enableSystem>
      <WallCrunch />
    </ThemeProvider>
  );
}

// WallCrunch core logic component
function WallCrunch() {
  // State to hold the selected bright and dark wallpaper files
  const [brightWallpaper, setBrightWallpaper] = useState<File | null>(null);
  const [darkWallpaper, setDarkWallpaper] = useState<File | null>(null);
  // Get the current theme and setter from next-themes, useful for debugging or future theme toggles
  const { theme, setTheme } = useTheme();

  // Function to handle picking a wallpaper file
  const handlePickWallpaper = (type: "bright" | "dark") => {
    const input = document.createElement("input");
    input.type = "file";
    input.accept = "image/*"; // Accept only image files

    // Event listener for when a file is selected
    input.onchange = (e: Event) => {
      const target = e.target as HTMLInputElement;
      const file = target.files?.[0] || null; // Get the first selected file

      // Set the appropriate wallpaper state based on type
      if (type === "bright") {
        setBrightWallpaper(file);
      } else {
        setDarkWallpaper(file);
      }
    };
    input.click(); // Programmatically click the hidden input to open file dialog
  };

  // Function to clear both selected wallpapers
  const handleClear = () => {
    setBrightWallpaper(null);
    setDarkWallpaper(null);
  };

  // Placeholder for save functionality (will be implemented with electronAPI as per original comment)
  const handleSave = () => {
    // Logic for saving wallpapers will go here
    console.log("Saving wallpapers...", { brightWallpaper, darkWallpaper });
    // This part would typically interact with a backend or local system API (e.g., Electron)
  };

  return (
    // Main container for the WallCrunch application
    // The background gradient adapts based on the applied theme class ('dark:' prefix for dark mode)
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 dark:from-slate-900 dark:to-slate-800 flex flex-col items-center justify-center p-8 relative font-inter">
      {/* Display current theme for debugging/testing */}
      <div className="absolute top-20 right-4 text-sm text-gray-500 dark:text-gray-400">
        Current Theme: {theme}
      </div>

      {/* Close Button (placeholder functionality) */}
      <button
        className="absolute top-4 right-4 p-2 rounded-full hover:bg-red-100 dark:hover:bg-red-900 transition-colors duration-200"
        aria-label="Close"
        onClick={() => { console.log("Close button clicked"); }}
      >
        <X className="w-5 h-5 text-red-500" />
      </button>

      {/* Settings Button (placeholder functionality) */}
      <button
        className="absolute top-4 left-4 p-2 rounded-full hover:bg-slate-200 dark:hover:bg-slate-800 transition-colors duration-200"
        aria-label="Settings"
        onClick={() => { console.log("Settings button clicked"); }}
      >
        <Settings className="w-5 h-5 text-gray-600 dark:text-gray-300" />
      </button>

      {/* Main Card component for the application UI */}
      <Card className="w-full max-w-md rounded-xl shadow-lg dark:border dark:border-slate-700">
        <CardHeader className="pb-4">
          <CardTitle className="text-center text-4xl font-extrabold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent py-2">
            WallCrunch
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-6 pt-0">
          {/* Section for picking wallpapers */}
          <div className="flex flex-col gap-4">
            <Button
              variant="outline"
              className="w-full h-12 text-base rounded-lg shadow-sm hover:shadow-md transition-all duration-200 dark:border-slate-600 dark:hover:bg-slate-700 dark:hover:text-white"
              onClick={() => handlePickWallpaper("bright")}
            >
              {brightWallpaper ? (
                <span className="truncate">
                  Bright Wallpaper: <span className="font-semibold text-blue-700 dark:text-blue-300">{brightWallpaper.name}</span>
                </span>
              ) : (
                "Pick Bright Wallpaper"
              )}
            </Button>
            <Button
              variant="outline"
              className="w-full h-12 text-base rounded-lg shadow-sm hover:shadow-md transition-all duration-200 dark:border-slate-600 dark:hover:bg-slate-700 dark:hover:text-white"
              onClick={() => handlePickWallpaper("dark")}
            >
              {darkWallpaper ? (
                <span className="truncate">
                  Dark Wallpaper: <span className="font-semibold text-purple-700 dark:text-purple-300">{darkWallpaper.name}</span>
                </span>
              ) : (
                "Pick Dark Wallpaper"
              )}
            </Button>
          </div>

          {/* Action buttons (Clear and Save) */}
          <div className="flex gap-4">
            <Button
              variant="secondary"
              className="flex-1 h-12 text-base rounded-lg shadow-sm hover:shadow-md transition-all duration-200 dark:hover:bg-slate-700 dark:hover:text-white"
              onClick={handleClear}
              disabled={!brightWallpaper && !darkWallpaper}
            >
              Clear
            </Button>
            <Button
              className="flex-1 h-12 text-base rounded-lg shadow-sm hover:shadow-md transition-all duration-200 bg-blue-600 hover:bg-blue-700 text-white disabled:opacity-50 disabled:cursor-not-allowed dark:bg-purple-600 dark:hover:bg-purple-700"
              onClick={handleSave}
              disabled={!brightWallpaper || !darkWallpaper}
            >
              Save
            </Button>
          </div>

          {/* Optional: Theme toggle for demonstration (can be removed if only system theme is desired) */}
          <div className="flex justify-center pt-4">
            <Button
              variant="ghost"
              className="text-sm text-gray-600 dark:text-gray-400 hover:bg-slate-200 dark:hover:bg-slate-700 rounded-md px-3 py-2"
              onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
            >
              Toggle Theme (Current: {theme})
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}

